@font-face {
  font-family: "Bebas Neue";
  font-weight: 400;
  font-style: normal;
  src: local("Bebas Neue Regular"), local("BebasNeue-Regular"), url("../fonts/bebas-neue.woff2") format("woff2"), url("../fonts/bebas-neue.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0; }

html {
  box-sizing: border-box;
  height: 100%; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  line-height: 140%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: #102654; }

img {
  max-width: 100%;
  height: auto; }

input,
textarea {
  border-radius: 0; }

.logo {
  letter-spacing: 0.02em;
  color: #ffffff;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-size: 26px;
  line-height: 26px; }

@media (max-width: 1199px) {
