.button {
  align-items: center;
  width: 230px;
  min-height: 61px;
  cursor: pointer;
  transition: border 0.5s ease, color 0.5s ease;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 4px solid #ffffff;
  background-color: transparent;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px; }
  .button:hover {
    color: #102654;
    border: 4px solid #102654; }

@media (max-width: 1199px) {
  .button {
    width: 183px; } }

@media (max-width: 767px) {
  .button {
    width: 100%; } }

